import Swal from "sweetalert2";
import { berhasil } from "./Alert";

export const checkReplikasiValidasi = (lokasi, data) => {
  var searchData = JSON.parse(localStorage.getItem(lokasi)) || [];
  var index = searchData.findIndex(function (item, i) {
    return item.noFaktur === data;
  });
  return index;
};
export const checkRekapBon = (lokasi, data) => {
  var searchData = JSON.parse(localStorage.getItem(lokasi)) || [];
  var index = searchData.findIndex(function (item, i) {
    return item.namaAktor === data ? item.namaAktor === data : "TIDAK ADA";
  });
  return index;
};

export const addFaktur = (
  noFaktur,
  tanggal,
  noBon,
  supplier,
  bruto,
  netto,
  bonRp,
  titipRp,
  titipGr,
  keterangan,
  e
) => {
  var tampung = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
  var hasil = checkReplikasiValidasi("FakturTerpilih", noFaktur);
  if (hasil === -1) {
    var array = {
      tanggal: tanggal,
      noBon: noBon,
      supplier: supplier,
      bruto: bruto,
      netto: netto,
      bonRp: bonRp,
      titipRp: titipRp,
      titipGr: titipGr,
      keterangan: keterangan,
      noFaktur: noFaktur,
      footerDocument: {
        editBy: "Nagatech",
        statusValid: "VALID",
      },
    };
    tampung.push(array);
    localStorage.setItem("FakturTerpilih", JSON.stringify(tampung));
    console.log("BERHASIL TAMBAHKAN : " + noFaktur);
    berhasil("Berhasil Ditambahkan");
  } else {
    Swal.fire({
      position: "top-right",
      icon: "warning",
      text: "Faktur Sudah Terpilih",
      showConfirmButton: false,
      timer: 1500,
    });
  }
};
