export const addRekapBon = (noFaktur, tanggal, bruto, netto, rp, namaAktor) => {
  var tampung = JSON.parse(localStorage.getItem("FakturRekapBon")) || [];
  var hasil = checkReplikasiValidasi("FakturRekapBon", noFaktur);
  if (hasil < 1) {
    var hasilAktor = checkRekapBon("FakturRekapBon", namaAktor);
    if (hasilAktor < 1) {
      var array = {
        tanggal: tanggal,
        noFakturPilih: noFaktur,
        namaAktor: namaAktor,
        bruto: bruto,
        netto: netto,
        rp: rp,
      };
      tampung.push(array);
      localStorage.setItem("FakturRekapBon", JSON.stringify(tampung));
      console.log("BERHASIL TAMBAHKAN : " + noFaktur);
      berhasil("Berhasil Ditambahkan");
    } else {
      Swal.fire({
        position: "top-right",
        icon: "warning",
        text: "Nama Customer Berbeda",
        showConfirmButton: false,
        timer: 1500,
      });
    }
  } else {
    Swal.fire({
      position: "top-right",
      icon: "warning",
      text: "Faktur Sudah Terpilih",
      showConfirmButton: false,
      timer: 1500,
    });
  }
};

export const deleteFaktur = (noFaktur) => {
  var tampung = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
  var hasil = checkReplikasiValidasi("FakturTerpilih", noFaktur);
  tampung.splice(hasil, 1);
  localStorage.setItem("FakturTerpilih", JSON.stringify(tampung));
  berhasil("berhasil dihapus");
  console.log("HASIL TERBARU", JSON.stringify(tampung));
};
