import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import {
  getDataSelectedValidasiSupplier,
  getValidasiSupplierPembayaranHutangSupplier,
  getValidasiSupplierPenerimaanBarangSupplier,
  getValidasiSupplierReturnSupplier,
  getValidasiSupplierTitipSupplier,
  getValidasiSupplierTolakanPembayaranSupplier,
} from "../actions/validasiSupplierAction";
import { addFaktur, checkReplikasiValidasi, deleteFaktur } from "../FindIndex";
import Axios from "axios";
import Swal from "sweetalert2";
import { gagal } from "../Alert";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptostate = (state) => {
  return {
    listValidasiSupplier: state.provinsi.listValidasiSupplier,
    listDataSelectedValidasiSupplier:
      state.provinsi.listDataSelectedValidasiSupplier,
  };
};

const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class validasiSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Supplier",
        },
        {
          dataField: "bruto",
          text: "Bruto",
        },
        {
          dataField: "netto",
          text: "Netto",
        },
        {
          dataField: "bonRp",
          text: "Bon (Rp)",
        },
        {
          dataField: "titipGr",
          text: "Titip (Gr)",
        },
        {
          dataField: "titipRp",
          text: "Titip (Rp)",
        },
        {
          dataField: "Keterangan",
          text: "Keterangan",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            var hasil = checkReplikasiValidasi("FakturTerpilih", row.noFaktur);
            if (hasil === -1) {
              return (
                <div className="text-center">
                  <button
                    className="btn btn-warning mr-2"
                    onClick={(e) =>
                      addFaktur(
                        row.noFaktur,
                        row.tanggal,
                        row.noBon,
                        row.supplier,
                        row.bruto,
                        row.netto,
                        row.bonRp,
                        row.titipRp,
                        row.titipGr,
                        row.keterangan,
                        this.updateButton(this.state.tipeTransaksi),
                        this.disabledButton(e)
                      )
                    }
                  >
                    <i className="fa fa-check-circle mr-1"></i>
                    Pilih
                  </button>
                </div>
              );
            } else {
              return (
                <div>
                  <button
                    className="btn btn-warning mr-2"
                    onClick={() =>
                      deleteFaktur(
                        row.noFaktur,
                        this.updateButton(this.state.tipeTransaksi)
                      )
                    }
                  >
                    <i className="fa fa-times mr-1"></i>
                    Batal
                  </button>
                </div>
              );
            }
          },
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
          noBon: "TESTING-123",
          noFaktur: "FAKTUR-123",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
    };
  }

  updateButton(data) {
    switch (data) {
      case "Penerimaan Barang Supplier":
        this.props.dispatch(getValidasiSupplierPenerimaanBarangSupplier());
        break;
      case "Return Supplier":
        this.props.dispatch(getValidasiSupplierReturnSupplier());
        break;
      case "Tolakan Return Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Pembayaran Hutang Supplier":
        this.props.dispatch(getValidasiSupplierPembayaranHutangSupplier());
        break;
      case "Tolakan Pembayaran Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Titip Supplier":
        this.props.dispatch(getValidasiSupplierTitipSupplier());
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  disabledButton = (e) => {
    e.target.disabled = true;
  };

  changeTransaksi = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    switch (e.target.value) {
      case "Penerimaan Barang Supplier":
        this.props.dispatch(getValidasiSupplierPenerimaanBarangSupplier());
        break;
      case "Return Supplier":
        this.props.dispatch(getValidasiSupplierReturnSupplier());
        break;
      case "Tolakan Return Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Pembayaran Hutang Supplier":
        this.props.dispatch(getValidasiSupplierPembayaranHutangSupplier());
        break;
      case "Tolakan Pembayaran Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Titip Supplier":
        this.props.dispatch(getValidasiSupplierTitipSupplier());
        break;
      default:
        console.log("ERROR");
        break;
    }
  };
  sendValidasi(data) {
    var hasil = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
    switch (data) {
      case "Penerimaan Barang Supplier":
        Axios.put(server + "spp/dc/trm", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPenerimaanBarangSupplier()
                )
              )
          )
          .catch((err) => gagal("Validasi Gagal"));
        break;
      case "Return Supplier":
        Axios.put(server + "spp/dc/rtr", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPenerimaanBarangSupplier()
                )
              )
          )
          .catch((err) =>
            Swal.fire({
              position: "top-right",
              icon: "error",
              text: "Tampaknya ada sedikit kendala, Mohon Periksa Koneksi Anda",
              showConfirmButton: false,
              timer: 2500,
            })
          );
        break;
      case "Tolakan Return Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Pembayaran Hutang Supplier":
        Axios.put(server + "spp/dc/byr", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPembayaranHutangSupplier()
                )
              )
          )
          .catch((err) =>
            Swal.fire({
              position: "top-right",
              icon: "error",
              text: "Tampaknya ada sedikit kendala, Mohon Periksa Koneksi Anda",
              showConfirmButton: false,
              timer: 2500,
            })
          );
        break;
      case "Tolakan Pembayaran Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Titip Supplier":
        Axios.put(server + "spp/dc/ttp", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPembayaranHutangSupplier()
                )
              )
          )
          .catch((err) =>
            Swal.fire({
              position: "top-right",
              icon: "error",
              text: "Tampaknya ada sedikit kendala, Mohon Periksa Koneksi Anda",
              showConfirmButton: false,
              timer: 2500,
            })
          );
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getDataSelectedValidasiSupplier());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };
  changeInput = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        console.log(row.noFaktur);
        let array = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
        const data = {
          tanggal: row.tanggal,
          noBon: row.noBon,
          supplier: row.namaAktor,
          bruto: row.bruto,
          netto: row.netto,
          bonRp: "",
          titipRp: "",
          titipGr: "",
          keterangan: row.keterangan,
          noFaktur: row.noFaktur,
          footerDocument: {
            editBy: "Nagatech",
            statusValid: "VALID",
          },
        };
        if (isSelect) {
          array.push(data);
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
        } else {
          var index = array.findIndex((item, i) => {
            return item.noFaktur === row.noFaktur;
          });
          array.splice(index, 1);
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        var array = [];
        rows.forEach(function (list) {
          var data = {
            tanggal: list.tanggal,
            noBon: list.noBon,
            supplier: list.namaAktor,
            bruto: list.bruto,
            netto: list.netto,
            bonRp: "",
            titipRp: "",
            titipGr: "",
            keterangan: list.keterangan,
            noFaktur: list.noFaktur,
            footerDocument: {
              editBy: "Nagatech",
              statusValid: "VALID",
            },
          };
          array.push(data);
        });
        if (isSelect) {
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
        } else {
          localStorage.removeItem("FakturTerpilih");
        }
      },
    };
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-3">
                      <label htmlFor="">Pilih Jenis Transaksi</label>
                      <Field
                        name="tipeTransaksi"
                        label="Dari"
                        component="select"
                        className="form-control"
                        onChange={this.changeTransaksi("tipeTransaksi")}
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        <option
                          value="Penerimaan Barang Supplier"
                          key="Penerimaan Barang Supplier"
                        >
                          Penerimaan Barang Supplier
                        </option>
                        <option value="Return Supplier" key="Return Supplier">
                          Return Supplier
                        </option>
                        <option
                          value="Tolakan Return Supplier"
                          key="Tolakan Return Supplier"
                        >
                          Tolakan Return Supplier
                        </option>
                        <option
                          value="Pembayaran Hutang Supplier"
                          key="Pembayaran Hutang Supplier"
                        >
                          Pembayaran Hutang Supplier
                        </option>
                        <option
                          value="Tolakan Pembayaran Supplier"
                          key="Tolakan Pembayaran Supplier"
                        >
                          Tolakan Pembayaran Supplier
                        </option>
                        <option value="Titip Supplier" key="Titip Supplier">
                          Titip Supplier
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="supplier"
                        label="Supplier"
                        component={renderField}
                        type="text"
                        onChange={this.changeInput("supplier")}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="noBon"
                        label="Nomor Bon"
                        component={renderField}
                        type="text"
                        onChange={this.changeInput("noBon")}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="tanggal"
                        label="Tanggal Transaksi"
                        component={renderField}
                        type="date"
                        onChange={this.changeInput("tanggalFrom")}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button className="btn btn-primary">
                          <i className="fa fa-eye"></i> Lihat Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      {this.props.listValidasiSupplier ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.listValidasiSupplier}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                                selectRow={selectRow}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12">
                      {this.props.listDataSelectedValidasiSupplier ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.listDataSelectedValidasiSupplier}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="row">
                        <div className="col-lg-6">
                          <div className="text-left">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.prevStep()}
                            >
                              <i className="fa fa-chevron-left mr-3"></i>Back
                            </button>
                          </div>
                        </div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              onClick={() =>
                                this.sendValidasi(this.state.tipeTransaksi)
                              }
                            >
                              <i className="fa fa-check-circle mr-3"></i>
                              VALIDASI
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

validasiSupplier = reduxForm({
  form: "validasiSupplier",
  enableReinitialize: true,
})(validasiSupplier);
export default connect(maptostate, null)(validasiSupplier);
